const baseSpeed: number = 4;
const timeSpeedMs: number = 50;
const hitErrorMarginPx: number = 18;

enum MovementKey {
    ArrowUp,
    ArrowRight,
    ArrowDown,
    ArrowLeft,
}

const DirectionProperty = new Map<MovementKey, 'top' | 'left'> ([
    [MovementKey.ArrowUp, 'top'],
    [MovementKey.ArrowRight, 'left'],
    [MovementKey.ArrowDown, 'top'],
    [MovementKey.ArrowLeft, 'left'],
]);

const DirectionSpeed = new Map<MovementKey, number> ([
    [MovementKey.ArrowUp, -baseSpeed],
    [MovementKey.ArrowRight, +baseSpeed],
    [MovementKey.ArrowDown, +baseSpeed],
    [MovementKey.ArrowLeft, -baseSpeed],
]);

interface Position {
    top: number;
    left: number;
}

var currentDirection: 'top'|'left'|null = null;
var currentSpeed = baseSpeed;

var timer = setInterval(function(){ Time(); }, timeSpeedMs); 
// clearInterval(timer);

document.onkeydown = function (this: GlobalEventHandlers, ev : KeyboardEvent): any {
    var keyCode: string = "";

    if (ev.key !== undefined) {
        keyCode = ev.key;
    } else if ((<any>ev).keyIdentifier !== undefined) {
        keyCode = (<any>ev).keyIdentifier;
    } else if (ev.keyCode !== undefined) {
        keyCode = ev.keyCode.toString();
    }

    if (keyCode in MovementKey) {
        let move = MovementKey[keyCode as keyof typeof MovementKey]

        SetMove(DirectionProperty.get(move)!, DirectionSpeed.get(move)!);
    }
}

function SetMove(cssProperty: 'top' | 'left', speed: number) {
    currentDirection = cssProperty;
    currentSpeed = speed;
}

function Time() {
    if (currentDirection == null)
        return;

    let head = MoveHead(currentDirection, currentSpeed);

    if (CheckIfHitFood(head)) {
        console.log('food was hit');
    }
}

function MoveHead(cssProperty: 'top' | 'left', speed: number): Position {
    let head = <HTMLDivElement> document.querySelector('div#head');

    let headStyle = getComputedStyle(head);

    head.style[cssProperty] = `${parseInt(headStyle[cssProperty]) + speed}`;

    let top = parseInt(headStyle['top']);
    let left = parseInt(headStyle['left']);
    return { top, left };
}

function CheckIfHitFood(head: Position) {
    let food = <HTMLDivElement>document.querySelector('#food');
    let foodStyle = getComputedStyle(food);

    let top = parseInt(foodStyle['top']);
    let left = parseInt(foodStyle['left']);

    if((head.top > top - hitErrorMarginPx && head.top < top + hitErrorMarginPx) &&
        (head.left > left - hitErrorMarginPx && head.left < left + hitErrorMarginPx))
        return true;

    return false;
}